$spacer: 10px;

// colors
.segment.secondary {
    background-color: #ebe3de !important;
}

.opacity-0 {
    opacity: 0;
    filter: alpha(opacity=0); /* For IE8 and earlier */
}

.opacity-25 {
    opacity: 0.25;
    filter: alpha(opacity=25); /* For IE8 and earlier */
}

.opacity-50 {
    opacity: 0.5;
    filter: alpha(opacity=50); /* For IE8 and earlier */
}

.opacity-75 {
    opacity: 0.75;
    filter: alpha(opacity=75); /* For IE8 and earlier */
}

// text
.menuFont {
    font-size: 18px;
}

.text-break {
    overflow: hidden;
    overflow-wrap: break-word;
}

// spacing
// concrete spacing
.main {
    margin-left: 160px;
}

// padding
.p-0 {
    padding: 0 !important;
}

.p-1 {
    padding: ($spacer * 0.25) !important;
}

.p-2 {
